import { useMemo } from "react"
import { useUser } from "@clerk/clerk-react"
import { useWatchlist } from "./useWatchlist"

const useWatchlistsToCustomerIO = (watchlistId: string | undefined) => {
  const { user } = useUser()
  const { data } = useWatchlist(watchlistId)
  const watchlists = useMemo(() => {
    if (!data) return []
    return [
      {
        id: data.id,
        ownerIsUser: data.ownerUserId === user?.id,
        ownerId: data.ownerUserId,
      },
    ]
  }, [user?.id, data])

  return { watchlists }
}

export { useWatchlistsToCustomerIO }
